import { reduxConsts as consts } from '../../constants/app.consts';

const initialState = {
    fileStructure: {}
}

const appReducer = (state = initialState, action) => {
    const { type, payload } = action;
    switch(type){
        case consts.SET_FILE_STRUCTURE:
            return {
                ...state,
                fileStructure: { ...payload }
            };
        default:
            return state;
    }
};

export default appReducer;