import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { ItemStyleIcon } from "../WindowContainer";
import './index.scss';

const Desktop = ({
    content,
    openWindow,
    contextMenu
}) => {
    const [ iconList, setIconList ] = useState([]);
    const fileStructure = useSelector(state => state.app.fileStructure);
    useEffect(() => {
        let tempList = [];
        if(content){
            Object.keys(content).forEach((element,index) => {
                tempList.push(
                    <ItemStyleIcon
                        key={index}
                        item={content[element]}
                        clickAction={openWindow}
                        path={'/Desktop/'}
                        contextMenu={contextMenu}
                    />
                );
            })
            setIconList(tempList);
        }
    },[content, fileStructure]);
    return (
        <>
            <div className="desktop-icons">
                {iconList}
            </div>
        </>
    );
}

export default Desktop;