import './App.css';
import { useEffect, useState } from 'react';
import Desktop from './components/Desktop';
import Window from './components/Window';
import ReactDOM from 'react-dom';
import ContextMenu from './components/common/ContextMenu';
import { Provider, useDispatch, useSelector } from 'react-redux';
import { setFileStructure, createFolder } from './redux/actions';

function App({ store }) {
  const dispatch = useDispatch();
  const fileStructure = useSelector(state => state.app.fileStructure);
  const [ showContextMenu, setShowContextMenu ] = useState(false);
  const [ contextMenuOptions, setContextMenuOptions ] = useState({});
  const [ contextMenuPath, setContextMenuPath ] = useState({
    name: 'root',
    id: '',
    path: '/'
  });
  const openWindow = (title,content,details,path) => {
    let windowElement = document.createElement('div');
    windowElement.classList.add("window-container");
    document.body.appendChild(windowElement);
    const handleOnClose = () => {
      if(windowElement)
        windowElement.remove();
    }
    ReactDOM.render(
      <Provider store={store}>
        <Window
          content={content}
          path={path}
          title={title}
          onClose={handleOnClose}
          contextMenu={
            (e,pathDetails) => {
              setContextMenuPath(pathDetails);
              handleContextMenu(e,rightClickMenuOptions)
            }
          }
          itemContextMenu={
            (event,menuOptions) => {
              handleContextMenu(event,menuOptions);
            }
          }
        />
      </Provider>,
      windowElement
    );
  }

  const rightClickMenuOptions = {
    "create_folder":{
      name: "New Folder",
      type: "option",
      check: (details) => {
          if(details.canCreateFolder === false)
              return true;
          return false;
      },
      action: (details) => {
        dispatch(createFolder(fileStructure, details.path));
      }
    },
    "separator_1":{
      type: "separator"
    },
    "get_info":{
      name: "Get Info",
      type: "option",
      action: () => {}
    },
    "report_a_bug":{
      name: "Report a bug",
      type: "option",
      action: () => {}
    }
  }

  const handleContextMenuClose = () => {
    setShowContextMenu(false);
    setContextMenuOptions(false);
    window.removeEventListener('click',handleContextMenuClose);
  }

  const handleContextMenu = (event,menuOptions) => {
    event.preventDefault();
    setContextMenuOptions(menuOptions);
    setShowContextMenu({
      x: event.nativeEvent?event.nativeEvent.x:event.x,
      y: event.nativeEvent?event.nativeEvent.y:event.y
    });
    window.addEventListener('click',handleContextMenuClose);
  }
  
  const clearDefaultEvent = (event) => {
    event.preventDefault();
  }
  
  useEffect(() => {
    window.addEventListener('contextmenu',clearDefaultEvent);
    console.clear();
    dispatch(setFileStructure({
      "recent links": {
          type: "generic-folder",
          name: "Recent Links",
          canCreateFolder: false,
          canDeleteFolder: false,
          canRenameFolder: false,
          content: {
              "today": {
                  type: "generic-folder",
                  name: "Today",
                  canCreateFolder: false,
                  canDeleteFolder: false,
                  canRenameFolder: false,
                  content: {}
              },
              "yesterday": {
                  type: "generic-folder",
                  name: "Yesterday",
                  canCreateFolder: false,
                  canDeleteFolder: false,
                  canRenameFolder: false,
                  content: {}
              }
          }
      },
      "bookmarks": {
          type: "generic-folder",
          name: "Bookmarks",
          canDeleteFolder: false,
          canCreateFolder: false,
          canRenameFolder: false,
          content: {}
      },
      "blogs": {
          type: "generic-folder",
          name: "Blogs",
          canDeleteFolder: false,
          canCreateFolder: false,
          canRenameFolder: false,
          content: {}
      },
      "social links": {
          type: "generic-folder",
          name: "Social Links",
          canDeleteFolder: false,
          canCreateFolder: false,
          canRenameFolder: false,
          content: {}
      },
      "desktop": {
          type: "generic-folder",
          name: "Desktop",
          canDeleteFolder: false,
          canCreateFolder: true,
          canRenameFolder: false,
          content: {
            "new folder": {
              type: "generic-folder",
              name: "New Folder",
              content: {}
            },
            "new folder 1": {
              type: "generic-folder",
              name: "New Folder 1",
              content: {}
            },
            "new folder 2": {
              type: "generic-folder",
              name: "New Folder 2",
              content: {}
            }
          }
      }
    }));
    return function cleanup(){
      window.removeEventListener('contextmenu',clearDefaultEvent);
    }
  },[]);

  return (
    <section
      id="desktop-screen"
      onContextMenu={
        (e) => {
          setContextMenuPath({
            ...fileStructure['desktop'],
            path: '/Desktop/'
          });
          handleContextMenu(e,rightClickMenuOptions)
        }
      }
    >
      <Desktop
        content={fileStructure['desktop']?fileStructure['desktop'].content:{}}
        openWindow={openWindow}
        contextMenu={
          (event,menuOptions) => {
            handleContextMenu(event,menuOptions);
          }
        }
      />
      {
        showContextMenu && (
          <ContextMenu 
            x={showContextMenu.x}
            y={showContextMenu.y}
            options={contextMenuOptions}
            details={contextMenuPath}
            closeMenu={() => {setShowContextMenu(false);}}
          />
        )
      }
    </section>);
}

export default App;
