import './index.scss';

const MenuItem = ({
    item,
    details,
    disabled,
    closeMenu
}) => {
    const actionHandler = (e) => {
        if(typeof item.action === "function"){
            item.action(details);
            if(typeof closeMenu === "function"){
                closeMenu();
            }
        }
        else{
            switch(item.action){
                default:
                    e.preventDefault();
                    e.stopPropagation();
            }
        }
    }
    return (
        <div className={`menu-item ${item.type?item.type:''} ${disabled?'disabled':''}`} onClick={actionHandler}>
            {
                item.type === "option" && (
                    <>
                        <div className="tick">
                            {
                                item.selected && (
                                    <img alt="tick" src="./images/tick.png"/>
                                )
                            }
                        </div>
                        <p>{item.name}</p>
                    </>
                )
            }
            {
                item.type === "separator" && (
                    <hr className="separator"/>
                )
            }
        </div>
    );
}

export default MenuItem;