import MenuItem from '../MenuItem';
import './index.scss';

const ContextMenu = ({
    options,
    x = 0,
    y = 0,
    details,
    closeMenu
}) => {
    const handleBlankClick = (e) => {
        e.preventDefault();
        e.stopPropagation();
    }
    const handleCheck = (callback) => {
        if(typeof callback === 'function')
            return callback(details);
        return false;
    }
    return (
        <div
        id="context-menu"
        style={{
            top:y,
            left:x,
            transform:`translate(${(document.body.clientWidth-(x+200))<=0?'-100%':'0%'},${(document.body.clientHeight-(y+100))<=0?'-100%':'0%'})`
        }}
        onClick={handleBlankClick}>
            {
                options && Object.keys(options).map((element,index)=>{
                    return (
                        <MenuItem key={index} item={options[element]} details={details} disabled={handleCheck(options[element].check)} closeMenu={closeMenu}/>
                    );
                })
            }
        </div>
    );
}

export default ContextMenu;