const Toolbar = ({
    goBack,
    goForward,
    backward,
    forward,
    selectedIconStyle,
    setSelectedIconStyle,
    searchInput,
    searchInputFocus
}) => {
    const icons_show_as = {
        "icons": {
            className: "icon-type-icon-size",
            iconElement: (
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 70 70">
                    <path id="svg_2" d="m12.35241,35.359" opacity="0.5" strokeWidth="1.5"/>
                    <rect rx="4" id="svg_5" height="20.63208" width="21.3325" y="8.8331" x="7.70542" fillOpacity="null" strokeOpacity="null" strokeWidth="4" fill="transparent"/>
                    <rect rx="4" id="svg_13" height="20.74307" width="21.22151" y="40.16384" x="7.59443" fillOpacity="null" strokeOpacity="null" strokeWidth="4" fill="transparent"/>
                    <rect rx="4" id="svg_14" height="20.78579" width="20.89345" y="8.94409" x="40.88846" fillOpacity="null" strokeOpacity="null" strokeWidth="4" fill="transparent"/>
                    <rect rx="4" id="svg_15" height="20.96504" width="21.44348" y="40.20186" x="40.96208" fillOpacity="null" strokeOpacity="null" strokeWidth="4" fill="transparent"/>
                </svg>
            )
        },
        "list": {
            className: "list-size",
            iconElement: (
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid meet" viewBox="0 0 60 60">
                    <path d="" id="a4LPYj6orX"/>
                    <path d="M55.77 7.12C56.66 7.12 57.38 7.84 57.38 8.73C57.38 9.46 57.38 10.07 57.38 10.8C57.38 11.69 56.66 12.4 55.77 12.4C45.14 12.41 14.86 12.41 4.23 12.4C3.34 12.4 2.63 11.69 2.63 10.8C2.63 10.07 2.63 9.46 2.63 8.73C2.63 7.84 3.34 7.12 4.23 7.12C14.86 7.12 45.14 7.12 55.77 7.12Z" id="aLBD0I4be"></path>
                    <path d="M55.77 20.23C56.66 20.23 57.37 20.95 57.37 21.84C57.37 22.57 57.37 23.18 57.37 23.91C57.37 24.8 56.66 25.52 55.77 25.52C45.14 25.52 14.86 25.52 4.23 25.52C3.34 25.52 2.62 24.8 2.62 23.91C2.62 23.18 2.62 22.57 2.62 21.84C2.62 20.95 3.34 20.23 4.23 20.23C14.86 20.24 45.14 20.24 55.77 20.23Z" id="b2gwNPWFnX"></path>
                    <path d="M55.77 34.16C56.66 34.16 57.37 34.87 57.37 35.76C57.37 36.49 57.37 37.1 57.37 37.83C57.37 38.72 56.66 39.44 55.77 39.44C45.14 39.44 14.86 39.44 4.23 39.44C3.34 39.44 2.62 38.72 2.62 37.83C2.62 37.1 2.62 36.49 2.62 35.76C2.62 34.87 3.34 34.16 4.23 34.16C14.86 34.16 45.14 34.16 55.77 34.16Z" id="e2gIkJW7PK"></path>
                    <path d="M55.77 47.6C56.66 47.6 57.37 48.31 57.37 49.2C57.37 49.93 57.37 50.54 57.37 51.27C57.37 52.16 56.66 52.88 55.77 52.88C45.14 52.88 14.86 52.88 4.23 52.88C3.34 52.88 2.62 52.16 2.62 51.27C2.62 50.54 2.62 49.93 2.62 49.2C2.62 48.31 3.34 47.6 4.23 47.6C14.86 47.6 45.14 47.6 55.77 47.6Z" id="ctXYLCPAl"></path>
                </svg>
            )
        },
        "columns": {
            className: "window-size",
            iconElement: (
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid meet" viewBox="0 0 70 70">
                    <rect rx="3" id="svg_18" height="37.08042" width="62.26415" y="16.47951" x="3.86793" fillOpacity="null" strokeOpacity="null" strokeWidth="4" fill="transparent"/>
                    <rect id="svg_19" height="37.08042" width="22.53052" y="16.44007" x="23.73474" fillOpacity="null" strokeOpacity="null" strokeWidth="4" fill="transparent"/>
                </svg>
            )
        }
    }
    const changeIconStyle = (type) => {
        if(setSelectedIconStyle && typeof setSelectedIconStyle === "function"){
            setSelectedIconStyle(type);
        }
    }
    const handleBackButton = (e) => {
        e.preventDefault();
        e.stopPropagation();
        if(backward && backward.length){
            goBack();
        }
    }
    const handleForwardButton = (e) => {
        e.preventDefault();
        e.stopPropagation();
        if(forward && forward.length){
            goForward();
        }
    }
    const cancelEvents = (e) => {
        e.preventDefault();
        e.stopPropagation();
    }
    return (
        <div className="tool-bar">
            <div className="window-actions"
                title="See folders that you've viewed before"
                aria-label="See folders that you've viewed before">
                <div className={`back-button ${backward && backward.length?"":"disabled"}`}
                    onMouseDown={handleBackButton}
                    onDoubleClick={cancelEvents}>
                    <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 240.823 240.823">
                        <path id="Chevron_Right" d="M57.633,129.007L165.93,237.268c4.752,4.74,12.451,4.74,17.215,0c4.752-4.74,4.752-12.439,0-17.179
                            l-99.707-99.671l99.695-99.671c4.752-4.74,4.752-12.439,0-17.191c-4.752-4.74-12.463-4.74-17.215,0L57.621,111.816
                            C52.942,116.507,52.942,124.327,57.633,129.007z"/>
                    </svg>
                </div>
                <div className={`forward-button ${forward && forward.length?"":"disabled"}`}
                    onMouseDown={handleForwardButton}
                    onDoubleClick={cancelEvents}>
                    <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 240.823 240.823" style={{transform: "rotate(180deg)"}}>
                        <path id="Chevron_Right" d="M57.633,129.007L165.93,237.268c4.752,4.74,12.451,4.74,17.215,0c4.752-4.74,4.752-12.439,0-17.179
                            l-99.707-99.671l99.695-99.671c4.752-4.74,4.752-12.439,0-17.191c-4.752-4.74-12.463-4.74-17.215,0L57.621,111.816
                            C52.942,116.507,52.942,124.327,57.633,129.007z"/>
                    </svg>
                </div>
            </div>
            <div className="container-size">
                <div className="body-actions">
                    <div className="left">
                        <div className="icon-type-list"
                            title="Show items as icons, in a list, or in columns"
                            aria-label="Show items as icons, in a list, or in columns">
                            {
                                icons_show_as && Object.keys(icons_show_as).map((element, index) => (
                                    <div key={index}
                                    onClick={() => changeIconStyle(element)}
                                    onDoubleClick={cancelEvents}
                                    className={`${icons_show_as[element].className} ${selectedIconStyle === element?"active":""} ${index===0?"br-r-tl br-r-bl":""} ${(index + 1) === Object.keys(icons_show_as).length?"clip-l br-r-tr br-r-br":index !== 0?"clip-l clip- r":""}`}>
                                        {icons_show_as[element].iconElement}
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                    <div className="right">
                        <div className="search-bar" onMouseDown={searchInputFocus}>
                            <div className="search-icon">
                                <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 612.01 612.01">
                                    <path d="M606.209,578.714L448.198,423.228C489.576,378.272,515,318.817,515,253.393C514.98,113.439,399.704,0,257.493,0
                                        C115.282,0,0.006,113.439,0.006,253.393s115.276,253.393,257.487,253.393c61.445,0,117.801-21.253,162.068-56.586
                                        l158.624,156.099c7.729,7.614,20.277,7.614,28.006,0C613.938,598.686,613.938,586.328,606.209,578.714z M257.493,467.8
                                        c-120.326,0-217.869-95.993-217.869-214.407S137.167,38.986,257.493,38.986c120.327,0,217.869,95.993,217.869,214.407
                                        S377.82,467.8,257.493,467.8z"/>
                                </svg>
                            </div>
                            <div className="search-input">
                                <input type="text" name="search" placeholder="Search" autoComplete="off" ref={searchInput}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Toolbar;