import { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import Panel from '../Panel';
import Toolbar from '../Toolbar';
import WindowContainer from '../WindowContainer';
import './index.css';

const Window = ({
    path,
    title = "Finder",
    content,
    contextMenu,
    itemContextMenu,
    onClose
}) => {
    const windowRef = useRef();
    const windowBodyRef = useRef();
    const searchInput = useRef();
    const fileStructure = useSelector(state => state.app.fileStructure);
    const [ posX, setPosX ] = useState(25);
    const [ posY, setPosY ] = useState(25);
    const [ windowHeight, setWindowHeight ] = useState(400);
    const [ windowWidth, setWindowWidth ] = useState(500);
    const [ selectedIconStyle, setSelectedIconStyle ] = useState("icons");
    const [ backward, setBackward ] = useState([]);
    const [ forward, setForward ] = useState([]);
    const [ maximize, setMaximize ] = useState(false);
    const [ minimize, setMinimize ] = useState(false);
    const [ windowTitle, setWindowTitle ] = useState(title);
    const [ currentDirectoryDetails, setCurrentDirectoryDetails ] = useState({});
    const [ currentDirectory, setCurrentDirectory ] = useState({});
    const [ currentPath, setCurrentPath ] = useState(path);
    const [ focusOn, setFocusOn ] = useState(false);
    useEffect(() => {
        setCurrentDirectory(content);
        setCurrentDirectoryDetails({
            name: windowTitle,
            type: 'generic-folder',
            content: content
        })
        if(windowRef.current){
            handleWindowOnFocus();
        }
    },[content, windowRef]);
    const resizeMouseDown = (event, type) => {
        if(!checkLeftMouseClick(event))
            return;
        event.preventDefault();
        event.stopPropagation();
        const resizeWindow = (e) => {
            e.preventDefault();
            e.stopPropagation();
            let startPointX = event.clientX;
            let endPointX = e.clientX;
            let startPointY = event.clientY;
            let endPointY = e.clientY;
            let width = windowWidth + (startPointX - endPointX);
            let height = windowHeight + (startPointY - endPointY);
            let reverseWidth = windowWidth - (startPointX - endPointX);
            let reverseHeight = windowHeight - (startPointY - endPointY);
            let t_side_change, l_side_change;
            switch(type){
                case 'l-side':
                    l_side_change = width >= window.innerWidth? window.innerWidth: width >= 370?width: 370 ;
                    if(l_side_change !== 370)
                        setPosX((e.clientX * 100)/window.innerWidth)
                    setWindowWidth(l_side_change);
                    break;
                case 't-side':
                    t_side_change = height >= window.innerHeight?window.innerHeight:height >= 220?height: 220;
                    if(t_side_change !== 220)
                        setPosY((e.clientY * 100)/window.innerHeight);
                    setWindowHeight(t_side_change);
                    break;
                case 'r-side':
                    setWindowWidth(reverseWidth >= window.innerWidth? window.innerWidth:reverseWidth >= 370?reverseWidth: 370 );
                    break;
                case 'b-side':
                    setWindowHeight(reverseHeight >= window.innerHeight?window.innerHeight:reverseHeight >= 220?reverseHeight: 220 );
                    break;
                case 'tl-corner':
                    t_side_change = height >= window.innerHeight?window.innerHeight:height >= 220?height: 220;
                    if(t_side_change !== 220)
                        setPosY((e.clientY * 100)/window.innerHeight);
                    l_side_change = width >= window.innerWidth? window.innerWidth: width >= 370?width: 370 ;
                    if(l_side_change !== 370)
                        setPosX((e.clientX * 100)/window.innerWidth)
                    setWindowWidth(l_side_change);
                    setWindowHeight(t_side_change);
                    break;
                case 'tr-corner':
                    t_side_change = height >= window.innerHeight?window.innerHeight:height >= 220?height: 220;
                    if(t_side_change !== 220)
                        setPosY((e.clientY * 100)/window.innerHeight);
                    setWindowWidth(reverseWidth >= window.innerWidth? window.innerWidth:reverseWidth >= 370?reverseWidth: 370 );
                    setWindowHeight(t_side_change);
                    break;
                case 'br-corner':
                    setWindowWidth(reverseWidth >= window.innerWidth? window.innerWidth:reverseWidth >= 370?reverseWidth: 370 );
                    setWindowHeight(reverseHeight >= window.innerHeight?window.innerHeight:reverseHeight >= 220?reverseHeight: 220 );
                    break;
                case 'bl-corner':
                    l_side_change = width >= window.innerWidth? window.innerWidth: width >= 370?width: 370 ;
                    if(l_side_change !== 370)
                        setPosX((e.clientX * 100)/window.innerWidth)
                    setWindowWidth(l_side_change);
                    setWindowHeight(reverseHeight >= window.innerHeight?window.innerHeight:reverseHeight >= 220?reverseHeight: 220 );
                    break;
                default:
                    console.log("Event Type Not found!");
            }
        }
        const handleMouseUp = (e) => {
            window.removeEventListener('mousemove',resizeWindow);
            window.removeEventListener('mouseup',handleMouseUp);
        }
        window.addEventListener('mousemove',resizeWindow);
        window.addEventListener('mouseup',handleMouseUp);
    }
    const handleDragWindow = (event) => {
        event.preventDefault();
        event.stopPropagation();
        if(maximize)
            return
        const dragWindow = (e) => {
            e.preventDefault();
            e.stopPropagation();
            let endPointX = e.clientX - event.clientX;
            let endPointY = e.clientY - event.clientY;
            let offsetX = (posX/100)*window.innerWidth;
            let offsetY = (posY/100)*window.innerHeight;
            let relativeX = ((offsetX + endPointX)/window.innerWidth)*100;
            let relativeY = ((offsetY + endPointY)/window.innerHeight)*100;
            setPosX(relativeX);
            setPosY(relativeY);
        }
        const handleMouseUp = (e) => {
            window.removeEventListener('mousemove',dragWindow);
            window.removeEventListener('mouseup',handleMouseUp);
        }
        window.addEventListener('mousemove',dragWindow);
        window.addEventListener('mouseup',handleMouseUp);
    }
    const searchInputFocus = (event) => {
        event.stopPropagation();
        event.preventDefault();
        if(searchInput.current){
            searchInput.current.focus();
        }
    }
    const maximizeWindow = (event) => {
        event.stopPropagation();
        event.preventDefault();
        if(windowRef.current && windowBodyRef.current){
            windowRef.current.style.transition = "ease-in-out all 0.2s";
            windowBodyRef.current.style.transition = "ease-in-out all 0.2s";
            setTimeout(() => {
                if(windowRef.current && windowBodyRef.current){
                    windowRef.current.style.transition = "unset";
                    windowBodyRef.current.style.transition = "unset";
                }
            },300);
        }
        setMaximize(!maximize);
    }
    const minimizeWindow = (event) => {
        event.stopPropagation();
        event.preventDefault();
        console.log(minimize);
        if(windowRef.current && windowBodyRef.current){
            if(minimize){
                setTimeout(() => {
                    windowRef.current.style.transition = "unset";
                    windowBodyRef.current.style.transition = "unset";
                }, 300);
                windowRef.current.classList.remove("minimized");
            }
            else{
                windowRef.current.style.transition = "ease-in-out all 0.2s";
                windowBodyRef.current.style.transition = "ease-in-out all 0.2s";
                windowRef.current.classList.add("minimized");
            }
        }
        setMinimize(!minimize);
    }
    const changeDirectory = (title, directory, details, path) => {
        if(path === currentPath)
            return;
        setBackward([ ...backward, { ...currentDirectoryDetails, path: currentPath } ]);
        setCurrentDirectoryDetails({
            ...details,
            name: title,
            content: directory,
            path: currentPath
        });
        setCurrentPath(path);
        if(forward && forward.length > 0)
            setForward([]);
        setWindowTitle(title);
        setCurrentDirectory(directory);
    }
    const goBack = () => {
        if(backward.length > 0){
            let last = backward.length - 1;
            setForward([ ...forward, currentDirectoryDetails]);
            setWindowTitle(backward[last].name);
            setCurrentDirectory(backward[last].content);
            setCurrentPath(backward[last].path);
            setCurrentDirectoryDetails(backward[last]);
            backward.pop();
        }
    }
    const goForward = () => {
        if(forward.length > 0){
            let last = forward.length - 1;
            setBackward([ ...backward, currentDirectoryDetails ]);
            setWindowTitle(forward[last].name);
            setCurrentPath(forward[last].path);
            setCurrentDirectory(forward[last].content);
            setCurrentDirectoryDetails(forward[last]);
            forward.pop();
        }
    }
    const handleWindowOnFocus = () => {
        if(focusOn)
            return;
        if(window.windowFocusIndexCount){
            window.windowFocusIndexCount += 1
        }
        else
            window.windowFocusIndexCount = 1000
        if(windowRef.current)
            windowRef.current.style.zIndex = `${window.windowFocusIndexCount}`;
        if(!focusOn)
            setFocusOn(true);
    }
    const handleWindowFocusOut = () => {
        if(focusOn)
            setFocusOn(false);
    }
    const closeWindow = (e) => {
        e.preventDefault();
        e.stopPropagation();
        if(windowRef.current){
            windowRef.current.remove();
            if(typeof onClose === 'function'){
                onClose();
            }
        }
        if(!document.querySelector('.window')){
            window.windowFocusIndexCount = 1000
        }
    }
    const checkLeftMouseClick = (e) => {
        if(e.nativeEvent && e.nativeEvent.which === 1){
            return true;
        }
        else if(e.which === 1){
            return true;
        }
        return false;
    }
    return (
        <>
            <div className="window"
                ref={windowRef}
                style={{
                    maxHeight:`${maximize?window.innerHeight:windowHeight}px`,
                    maxWidth:`${maximize?window.innerWidth:windowWidth}px`,
                    minWidth: `${maximize?window.innerWidth:windowWidth}px`,
                    minHeight: `${maximize?window.innerHeight:windowHeight}px`,
                    top: `${maximize?0:posY}%`,
                    left: `${maximize?0:posX}%`,
                }}
                onClick={minimize?minimizeWindow:null}
                onMouseDownCapture={(e) => {windowRef.current.focus()}}
                onFocus={(e) => {handleWindowOnFocus()}}
                onBlur={(e) => {handleWindowFocusOut()}}
                tabIndex={0}
                >
                <div className="resizing-container">
                    <div className="t-side"
                        onMouseDown={(e) => { resizeMouseDown(e,'t-side') }}/>
                    <div className="l-side"
                        onMouseDown={(e) => { resizeMouseDown(e,'l-side') }}/>
                    <div className="r-side"
                        onMouseDown={(e) => { resizeMouseDown(e,'r-side') }}/>
                    <div className="b-side"
                        onMouseDown={(e) => { resizeMouseDown(e,'b-side') }}/>
                    <div className="tl-corner"
                        onMouseDown={(e) => { resizeMouseDown(e,'tl-corner') }}/>
                    <div className="tr-corner"
                        onMouseDown={(e) => { resizeMouseDown(e,'tr-corner') }}/>
                    <div className="bl-corner"
                        onMouseDown={(e) => { resizeMouseDown(e,'bl-corner') }}/>
                    <div className="br-corner"
                        onMouseDown={(e) => { resizeMouseDown(e,'br-corner') }}/>
                </div>
                <div className="frame br-b br-t br-r br-l br-r-tl br-r-tr"
                    onMouseDown={
                        (e) => {
                            if(checkLeftMouseClick(e))
                                handleDragWindow(e)
                        }
                    }
                    onDoubleClick={maximizeWindow}>
                    <div className="title-bar">
                        <div className="window-actions">
                            <div
                                className="close"
                                title="Close"
                                aria-label="Close"
                                onMouseDown={
                                    (e) => {
                                        if(checkLeftMouseClick(e))
                                            closeWindow(e)
                                    }
                                }>
                                <div className="button-text">
                                    <img alt="close-icon" src="./assets/icons/close.png"/>
                                </div>
                            </div>
                            <div
                                className="minimize"
                                title="Minimize"
                                aria-label="Minimize"
                                onMouseDown={
                                    (e) => {
                                        if(checkLeftMouseClick(e))
                                            minimizeWindow(e)
                                    }
                                }>
                                <div className="button-text">
                                    <img alt="minimize-icon" src="./assets/icons/minimize.png"/>
                                </div>
                            </div>
                            <div
                                className="maximize"
                                title="Maximize"
                                aria-label="Maximize"
                                onMouseDown={
                                    (e) => {
                                        if(checkLeftMouseClick(e))
                                            maximizeWindow(e);
                                    }
                                }>
                                <div className="button-text">
                                    <img alt="full-screen-icon" src="./assets/icons/full-screen.png"/>
                                </div>
                            </div>
                        </div>
                        <div className="title">
                            <img alt="generic-folder" src="./assets/icons/generic-folder.png"/>
                            <p className="ellipsis" title={windowTitle} aria-label={windowTitle}>{windowTitle}</p>
                        </div>
                    </div>
                    <Toolbar
                        goBack={goBack}
                        goForward={goForward}
                        backward={backward}
                        forward={forward}
                        selectedIconStyle={selectedIconStyle}
                        setSelectedIconStyle={setSelectedIconStyle}
                        searchInput={searchInput}
                        searchInputFocus={searchInputFocus}/>
                </div>
                <div className="body br-r br-l"
                    ref={windowBodyRef}
                    style={{
                        maxHeight:`${maximize?window.innerHeight-75-28:windowHeight-75-28}px`
                    }}>
                    <Panel fileStructure={fileStructure} path={'/'} changeDirectory={changeDirectory}/>
                    <WindowContainer
                        currentDirectory={currentDirectory}
                        currentDirectoryDetails={currentDirectoryDetails}
                        selectedIconStyle={selectedIconStyle}
                        changeDirectory={changeDirectory}
                        path={currentPath}
                        contextMenu={contextMenu}
                        itemContextMenu={itemContextMenu}
                    />
                </div>
                <div className="frame br-b br-t br-r br-l br-r-bl br-r-br">
                    <div className="status-bar">
                        <p>{ currentDirectory?Object.keys(currentDirectory).length:0} items</p>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Window;