import Icon from "../Icon";

const Panel = ({
    path,
    fileStructure,
    changeDirectory
}) => {
    return (
        <div className="panel br-r">
            <ul className="panel-list">
                {
                    fileStructure && Object.keys(fileStructure).map((element,index) => (
                        <li key={index} className="panel-list-item" onClick={() => {
                                changeDirectory(fileStructure[element].name,fileStructure[element].content,{
                                    ...fileStructure[element], name: undefined, content: undefined
                                },`${path}${fileStructure[element].name}/`)
                            }}>
                            <Icon type={fileStructure[element].type}/>
                            <p className="ellipsis">{fileStructure[element].name}</p>
                        </li>
                    ))
                }
            </ul>
        </div>
    );
}

export default Panel;