import { reduxConsts as consts } from '../../constants/app.consts';

const getId = (fileName) => {
    return fileName.toLowerCase();
}

const getIdFromPath = (path) => {
    try{
        const tokens = path.split('/').slice(1,-1);
        const folderIdList = [];
        tokens.forEach((element,index) => {
            folderIdList.push(getId(element));
        });
        return folderIdList;
    }
    catch(e){
        console.error(e);
        return [];
    }
}

const rename = (fileStructure, folderIdList, newName) => {
    try{
        if(folderIdList.length > 0){
            const tempId = folderIdList[0]
            const returnValue = rename(fileStructure[folderIdList[0]].content,folderIdList.slice(1), newName)
            if(returnValue){
                if(fileStructure[getId(newName)])
                    return -1;
                    //Case that folder already exists with that name
                fileStructure[tempId].name = newName;
                const tempStructure = fileStructure[tempId];
                delete fileStructure[tempId];
                fileStructure[getId(newName)] = tempStructure;
                return 0;
            }
            else{
                return returnValue;
            }
        }
        else{
            return 1;
        }
    }
    catch(e){
        return -1;
    }
}

export const renameFile = (fileStructure, path, newName) => {
    try{
        if(!newName.match(/^[a-zA-Z0-9\s]{1,}$/)){
            return {
                type: consts.SET_RENAME_ERROR,
                payload: "Please enter a name without any special characters!"
            }
        }
        else{
            const folderIdTokens = getIdFromPath(path);
            /**
             * I realized that you need to have DS basics
             * Trust me I was tired and I couldn't remember
             * DS Trees. I almost felt helpless for a while
             * But I'm a monster LOL
             */
            const returnValue = rename(fileStructure,folderIdTokens,newName);
            switch(returnValue){
                case -1:
                    return {
                        type: consts.SET_RENAME_ERROR,
                        payload: "A folder already exists with that name!"
                    }
                default:
                    return {
                        type: consts.SET_FILE_STRUCTURE,
                        payload: fileStructure?fileStructure:{}
                    }
            }
        }
    }
    catch(e){
        console.error(e);
        return {
            type: "IGNORE_ERROR_STATE"
        };
    }
}

export const removeRenameError = () => {
    return {
        type: consts.SET_RENAME_ERROR,
        payload: false
    }
}

const deleteFileWithTokens = (fileStructure, folderIdList) => {
    try{
        if(folderIdList.length > 0){
            const tempId = folderIdList[0]
            const returnValue = deleteFileWithTokens(fileStructure[folderIdList[0]].content,folderIdList.slice(1))
            if(returnValue){
                delete fileStructure[tempId];
                return 0;
            }
            else{
                return returnValue;
            }
        }
        else{
            return 1;
        }
    }
    catch(e){
        return -1;
    }
}

export const deleteFile = (fileStructure, path) => {
    try{
        const folderIdTokens = getIdFromPath(path);
        const returnValue = deleteFileWithTokens(fileStructure,folderIdTokens);
        switch(returnValue){
            case -1:
                return {
                    type: consts.SET_DELETE_ERROR,
                    payload: "Some Error Caused!"
                }
            default:
                return {
                    type: consts.SET_FILE_STRUCTURE,
                    payload: fileStructure?fileStructure:{}
                }
        }
    }
    catch(e){
        console.error(e);
        return {
            type: "IGNORE_ERROR_STATE"
        };
    }
}

const getNewFolderName = (fileStructure) => {
    let index = 0;
    while(true){
        if(!fileStructure[getId(`New Folder${index?` ${index}`:''}`)])
            return `New Folder${index?` ${index}`:''}`;
        else
            index += 1;
    }
}

const createFolderWithTokens = (fileStructure, folderIdList) => {
    try{
        if(folderIdList.length > 0){
            if(!fileStructure[folderIdList[0]].content){
                fileStructure[folderIdList[0]].content = {}
            }
            const returnValue = createFolderWithTokens(fileStructure[folderIdList[0]].content,folderIdList.slice(1))
            if(returnValue){
                return 0;
            }
            else{
                return returnValue;
            }
        }
        else{
            const newFolderName = getNewFolderName(fileStructure);
            fileStructure[getId(newFolderName)] = {
                type: "generic-folder",
                name: newFolderName,
                content: {}
            }
            return 1;
        }
    }
    catch(e){
        return -1;
    }
}

export const createFolder = (fileStructure, path) => {
    try{
        const folderIdTokens = getIdFromPath(path);
        const returnValue = createFolderWithTokens(fileStructure,folderIdTokens);
        switch(returnValue){
            case -1:
                return {
                    type: consts.SET_DELETE_ERROR,
                    payload: "Some Error Caused!"
                }
            default:
                return {
                    type: consts.SET_FILE_STRUCTURE,
                    payload: fileStructure?fileStructure:{}
                }
        }
    }
    catch(e){
        console.error(e);
        return {
            type: "IGNORE_ERROR_STATE"
        };
    }
}

export const setFileStructure = (fileStructure) => {
    return {
        type: consts.SET_FILE_STRUCTURE,
        payload: fileStructure?fileStructure:{}
    }
}