import { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { deleteFile, renameFile } from "../../redux/actions";
import Icon from "../Icon";
import './index.scss';

const WindowContainer = ({
    currentDirectory,
    currentDirectoryDetails,
    selectedIconStyle,
    changeDirectory,
    path,
    contextMenu,
    itemContextMenu
}) => {

    return (
        <div
            className="container-tab"
            data-path={path}
            onContextMenu={(e) => {
                e.preventDefault();
                e.stopPropagation();
                if(typeof contextMenu === "function"){
                    contextMenu(e, {
                        ...currentDirectoryDetails,
                        path: path
                    });
                }
            }}
            >
            <div className={`container-style-${selectedIconStyle}`}>
                {
                    selectedIconStyle === "icons" &&
                    currentDirectory && Object.keys(currentDirectory).map((element,index) => {
                        return (
                            <ItemStyleIcon
                                item={currentDirectory[element]}
                                key={index}
                                clickAction={changeDirectory}
                                path={path}
                                contextMenu={itemContextMenu}
                                />
                        );
                    })
                }
                {
                    selectedIconStyle === "list" && (
                        <table>
                            <colgroup>
                                <col style={{width:'10px'}}/>
                                <col style={{width:'25px'}}/>
                                <col style={{width:'40%'}}/>
                                <col style={{width:'40%'}}/>
                                <col style={{width:'20%'}}/>
                            </colgroup>
                            <tbody>
                                <tr className="item-style-list table-header">
                                    <th colSpan={3}><p>Name</p></th>
                                    <th colSpan={2}><p>Type</p></th>
                                </tr>
                                {
                                    currentDirectory && Object.keys(currentDirectory).map((element,index) => {
                                        return (
                                            <ItemStyleList
                                                item={currentDirectory[element]}
                                                key={index}
                                                clickAction={changeDirectory}
                                                path={path}/>
                                        );
                                    })
                                }
                            </tbody>
                        </table>
                    )
                }
            </div>
        </div>
    );
}

export const ItemStyleIcon = ({
    item,
    clickAction,
    path,
    contextMenu
}) => {
    const dispatch = useDispatch();
    const fileStructure = useSelector(state => state.app.fileStructure);
    const iconRef = useRef();
    const iconNameInputRef = useRef();
    const [ renameState, setRenameState ] = useState(false);
    const toggleFocus = () => {
        if(iconRef.current){
            if(iconRef.current.classList.contains("focus")){
                iconRef.current.classList.remove("focus");
            }
            else{
                iconRef.current.classList.add("focus");
            }
        }
    }
    const openFolder = () => {
        clickAction(item.name,item.content,{
            ...item, name: undefined, content: undefined
        },`${path}${item.name}/`);
    }
    const renameFolder = (e) => {
        e.preventDefault();
        if(iconNameInputRef.current.value && iconNameInputRef.current.value !== item.name){
            dispatch(renameFile(fileStructure,`${path}${item.name}/`,iconNameInputRef.current.value));
        }
        toggleFocus();
        setRenameState(false);
    }

    const deleteFolder = () => {
        if(item){
            toggleFocus();
            dispatch(deleteFile(fileStructure,`${path}${item.name}/`));
        }
    }
    const rightClickMenuOptions = {
        "open_folder":{
            name: "Open",
            type: "option",
            action: () => openFolder()
        },
        "separator_1":{
            type: "separator"
        },
        "rename_folder":{
            name: "Rename",
            type: "option",
            check: () => {
                if(item.canRenameFolder === false)
                    return true;
                return false;
            },
            action: () => { setRenameState(true) }
        },
        "delete_folder":{
            name: "Delete",
            type: "option",
            check: () => {
                if(item.canDeleteFolder === false)
                    return true;
                return false;
            },
            action: () => { deleteFolder() }
        }
    }

    useEffect(()=>{
        if(renameState){
            if(iconNameInputRef.current){
                iconNameInputRef.current.focus();
            }
        }
    },[renameState]);
    return (
        <div
            className="item-style-icon"
            title={item.name}
            aria-label={item.name}
            onDoubleClick={() => openFolder()}
            ref={iconRef}
            onFocus={toggleFocus}
            onBlur={toggleFocus}
            tabIndex={1}
            onContextMenu={(e) => {
                e.preventDefault();
                e.stopPropagation();
                if(typeof contextMenu === "function"){
                    contextMenu(e,rightClickMenuOptions);
                }
            }}
            >
            <Icon type={item.type}/>
            {
                renameState && (
                    <div className="rename-file-box">
                        <form
                            onSubmit={renameFolder}
                        >
                        <input 
                            type="text"
                            name="icon-name"
                            defaultValue={item.name}
                            autoComplete="off"
                            onBlur={(e) => { setRenameState(false) }}
                            ref={iconNameInputRef}
                        />
                        </form>
                    </div>
                )
            }
            {
                !renameState && (
                    <p className="icon-name">{item.name}</p>
                )
            }
        </div>
    );
}

export const ItemStyleList = ({
    item,
    clickAction,
    path
}) => {
    return (
        <tr
            className="item-style-list"
            onDoubleClick={() => clickAction(item.name,item.content,{
                ...item, name: undefined, content: undefined
            },`${path}${item.name}/`)}
            >
            <td></td>
            <td><Icon type={item.type}/></td>
            <td
            title={item.name}
            aria-label={item.name}
            >
                <p className="icon-name ellipsis">{item.name}</p></td>
            <td
            title={item.type}
            aria-label={item.type}
            ><p className="icon-data ellipsis">{item.type?item.type:''}</p></td>
            <td/>
        </tr>
    );
}
export default WindowContainer;